      //       span
      //         background-color: $color-invert
      //   .nav-menu
      //     .nav-item
 } }      //       border-top-color: rgba($color-invert, 0.2)
  // Sizes
  &.is-small {
    .hero-body {
      padding-bottom: 1.5rem;
      padding-top: 1.5rem; } }
  &.is-medium {
    @include tablet {
      .hero-body {
        padding-bottom: 9rem;
        padding-top: 9rem; } } }
  &.is-large {
    @include tablet {
      .hero-body {
        padding-bottom: 18rem;
        padding-top: 18rem; } } }
  &.is-halfheight,
  &.is-fullheight,
  &.is-fullheight-with-navbar {
    .hero-body {
      align-items: center;
      display: flex;
      & > .container {
        flex-grow: 1;
        flex-shrink: 1; } } }
  &.is-halfheight {
    min-height: 50vh; }
  &.is-fullheight {
    min-height: 100vh; } }

// Components

.hero-video {
  @extend %overlay;
  overflow: hidden;
  video {
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    position: absolute;
    top: 50%;
    transform: translate3d(-50%, -50%, 0); }
  // Modifiers
  &.is-transparent {
    opacity: 0.3; }
  // Responsiveness
  @include mobile {
    display: none; } }

.hero-buttons {
  margin-top: 1.5rem;
  // Responsiveness
  @include mobile {
    .button {
      display: flex;
      &:not(:last-child) {
        margin-bottom: 0.75rem; } } }
  @include tablet {
    display: flex;
    justify-content: center;
    .button:not(:last-child) {
      margin-right: 1.5rem; } } }

// Containers

.hero-head,
.hero-foot {
  flex-grow: 0;
  flex-shrink: 0; }

.hero-body {
  flex-grow: 1;
  flex-shrink: 0;
  padding: 3rem 1.5rem; }
